export class Invoice {
    id: number;
    invoice_code: string;
    patient_id: number;
    payment_type_id: number;
    date: string;
    total_price_ht: number;
    stamp_price: number;
    tva_amount: number;
    quotation_id:number;

    total_amount: number = 0;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    is_credit_note: boolean;
    invoice_code_if_credit_note: string;

    clear() {
        this.id = null;
        this.invoice_code = '';
        this.patient_id = null;
        this.payment_type_id = null;
        this.date = '';
        this.total_price_ht = 0;
        this.stamp_price = 0;
        this.tva_amount = 0;
        this.quotation_id = null;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
        this.is_credit_note = false;
        this.invoice_code_if_credit_note = null;
    }
}