import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_PATIENT = API_URL + '/api/v1/patients';
const API_AUDIOMETRY = API_URL + '/api/v1/audiometries';
const API_APPOINTMENT = API_URL + '/api/v1/patients_appointments'

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');



@Injectable()
export class PatientsService {
    constructor(private http: HttpClient) { }

    // add patient
    addPatient(patientPayload) {
        return this.http.post(API_PATIENT, patientPayload, { headers: httpHeaders });
    }
    getPatientByName(payload) {
        return this.http.post(API_PATIENT + '/getByFullName', payload, { headers: httpHeaders })
    }
    getPatient(patientId) {
        return this.http.get(API_PATIENT + '/' + patientId, { headers: httpHeaders })
    }
    addVisit(payload) {
        return this.http.post(API_AUDIOMETRY, payload, { headers: httpHeaders })
    }

    // get doctor by code
    getPatientByCode(patientcode) {
        return this.http.post(API_PATIENT + '/patient_code', { value: patientcode }, { headers: httpHeaders })
    }
    getAllPatients() {
        return this.http.get(API_PATIENT, { headers: httpHeaders })
    }
    updatePatient(patientId, payload) {
        return this.http.put(API_PATIENT + '/' + patientId, payload, { headers: httpHeaders })
    }
    getPatientSolde(patientId) {
        return this.http.get(API_PATIENT + '/getPatientSolde/' + patientId, { headers: httpHeaders })
    }

    // appointment
    addAppointment(payload) {
        return this.http.post(API_APPOINTMENT, payload, { headers: httpHeaders });
    }
    getAppointment(appointmentId) {
        return this.http.get(API_APPOINTMENT + '/' + appointmentId, { headers: httpHeaders })
    }
    getAllAppointments() {
        return this.http.get(API_APPOINTMENT, { headers: httpHeaders })
    }
    updateAppointment(appointmentId, payload) {
        return this.http.put(API_APPOINTMENT + '/' + appointmentId, payload, { headers: httpHeaders })
    }
}
