export class CreditNote {
    id: number;
    credit_note_code: string;
    patient_id: number;
    payment_type_id: number;
    date: string;
    total_price_ht: number;
    stamp_price: number;
    invoice_id:number;
    total_amount:number
    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.credit_note_code = '';
        this.patient_id = null;
        this.payment_type_id = null;
        this.date = '';
        this.total_price_ht = 0;
        this.stamp_price = 0;
        this.total_amount = 0;
        this.invoice_id = null;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}