import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_AFTER_SALE = API_URL + '/api/v1/after_sales';
const API_AFTER_SALE_HISTORY = API_URL + '/api/v1/after_sales_history';


const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class AfterSaleService {
    constructor(private http: HttpClient) { }

    addAfterSale(payload) {
        return this.http.post(API_AFTER_SALE, payload, { headers: httpHeaders });
    }

    getAllAfterSale() {
        return this.http.get(API_AFTER_SALE, { headers: httpHeaders });
    }

    addAfterSaleHistory(payload) {
        return this.http.post(API_AFTER_SALE_HISTORY, payload, { headers: httpHeaders });
    }

    getAllAfterSaleHistory() {
        return this.http.get(API_AFTER_SALE_HISTORY, { headers: httpHeaders });
    }

    getOpenAfterSales() {
        return this.http.get(API_AFTER_SALE + '/open', { headers: httpHeaders });
    }
    updateAfterSale(payload, id) {
        return this.http.put(API_AFTER_SALE + '/' + id, {afterSale: payload}, { headers: httpHeaders });
    }
    getPatientPurchases(patientId) {
        return this.http.get(API_AFTER_SALE + '/get_patients_purchases/' + patientId, { headers: httpHeaders });

    }

}