import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { API_URL } from '../../../../env';


const API_PRODUCT = API_URL + '/api/v1/products';
const API_QUOTATION = API_URL + '/api/v1/quotations';
const API_INVOICE = API_URL + '/api/v1/invoices';
const API_POSTED_INVOICE = API_URL + '/api/v1/invoices/posted';

const API_CREDIT_NOTE = API_URL + '/api/v1/credit_notes';
const API_DAILY_SALE = API_URL + '/api/v1/daily_sales';
const API_PATIENT_EMPLOYEE = API_URL + '/api/v1/user_patients_invoice';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class InvoicingService {
    constructor(private http: HttpClient) { }

    // add patient
    addProduct(Payload) {
        return this.http.post(API_PRODUCT, Payload, { headers: httpHeaders });
    }

    // get doctor by code
    getProductByCode(code) {
        // tslint:disable-next-line:prefer-const
        let Payload = { value: code };
        return this.http.post(API_PRODUCT + '/product_code', Payload, { headers: httpHeaders })
    }
    getAllProducts() {
        return this.http.get(API_PRODUCT, { headers: httpHeaders })
    }

    addQuotation(payload) {
        return this.http.post(API_QUOTATION, payload, { headers: httpHeaders });
    }

    addInvoice(payload) {
        return this.http.post(API_INVOICE, payload, { headers: httpHeaders });
    }

    
    addCreditNote(payload) {
        return this.http.post(API_CREDIT_NOTE, payload, { headers: httpHeaders });
    }


    getAllQuotations(payload) {
        return this.http.post(API_QUOTATION + '/two_dates', payload, { headers: httpHeaders });
    }

    getAllInvoices(payload) {
        return this.http.post(API_INVOICE + '/two_dates', payload, { headers: httpHeaders });
    }

    getAllPostedInvoices(payload) {
        return this.http.post(API_INVOICE + '/posted/two_dates', payload, { headers: httpHeaders });
    }

    getQuotation(id) {
        return this.http.get(API_QUOTATION + '/' + id, { headers: httpHeaders });
    }

    deleteQuotation(id) {
        return this.http.delete(API_QUOTATION + '/' + id, { headers: httpHeaders });
    }

    getInvoice(id) {
        return this.http.get(API_INVOICE + '/' + id, { headers: httpHeaders });
    }

    getPostedInvoice(id) {
        return this.http.get(API_POSTED_INVOICE + '/' + id, { headers: httpHeaders });
    }
    getInvoiceByCode(code) {
        return this.http.post(API_INVOICE + '/getByCode', { invoice_code: code }, { headers: httpHeaders });
    }
    addNomeclature(payload) {
        return this.http.post(API_PRODUCT + '/nomenclatures', payload, { headers: httpHeaders });
    }
    // tslint:disable-next-line:variable-name
    getProductNomenclature(product_id: any) {
        return this.http.post(API_PRODUCT + '/get_product_nomenclature', { value: product_id }, { headers: httpHeaders });
    }
    getSellsHistory(payload) {
        return this.http.post(API_INVOICE + '/invoices_history', payload, { headers: httpHeaders });
    }

    addDailySale(payload) {
        return this.http.post(API_DAILY_SALE, payload, { headers: httpHeaders });
    }
    addPatientEmployee(payload) {
        return this.http.post(API_PATIENT_EMPLOYEE, payload, { headers: httpHeaders });
    }

    getDailySalesHistory(payload) {
        return this.http.post(API_INVOICE + '/daily_sales_history', payload, { headers: httpHeaders });
    }

    getProduct(id) {
        return this.http.get(API_PRODUCT + '/' + id, { headers: httpHeaders });
    }

    updateProduct(id, payload) {
        return this.http.put(API_PRODUCT + '/' + id, payload, { headers: httpHeaders });
    }
    getNotAssignedInvoices(){
        return this.http.get(API_INVOICE + '/not_assigned_invoices', { headers: httpHeaders });

    }

}
