import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_ACCOUNT = API_URL + '/api/v1/accounts';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable
({
    providedIn: 'root'
  })
export class AccountsService {
    constructor(private http: HttpClient) { }
   

    // add patient
    addAccount(accountPayload) {
        return this.http.post(API_ACCOUNT, accountPayload, { headers: httpHeaders });
    }

    // get doctor by code
    getAccountByCode(accountCode) {
        return this.http.get(API_ACCOUNT + '/account_code/' + accountCode, { headers: httpHeaders })
    }
    getAllAccounts() {
        return this.http.get(API_ACCOUNT, { headers: httpHeaders })
    }
    get(accountCode) {
        return this.http.get(API_ACCOUNT + '/' + accountCode, { headers: httpHeaders })
    }
    updateAccount(accountcode, account) {
        return this.http.put(API_ACCOUNT + '/' + accountcode, account, { headers: httpHeaders })
    }
}