export class Doctor {
    id: number;
    doctor_code: string;
    gender_id: number;
    title_id: number;
    full_name: string;
    speciality_id: number;
    address: string;
    second_address: string;
    wilaya_id: number;
    town_id: number;
    phone_number: string;
    fax: string;
    mobile_phone_number_1: string;
    mobile_phone_number_2: string;
    email: string;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.doctor_code = '';
        this.gender_id = null;
        this.title_id = null;
        this.full_name = '';
        this.speciality_id = null;
        this.address = '';
        this.second_address = '';
        this.wilaya_id = null;
        this.town_id = null;
        this.phone_number = '';
        this.fax = '';
        this.mobile_phone_number_1 = '';
        this.mobile_phone_number_2 = '';
        this.email = '';
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }


}