export class PurchaseOrder {
    id: number;
    po_code: string;
    provider_id: number;
    po_type_id: number;
    creation_date: string;
    due_date: string;
    dutiable: boolean;
    tax_id: number;
    priority: boolean;
    total_amount: number;
    paid_amount: number;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.po_code = '';
        this.provider_id = null;
        this.po_type_id = null;
        this.creation_date = '';
        this.due_date = '';
        this.dutiable = false;
        this.tax_id = null;
        this.priority = false;
        this.total_amount = null;
        this.paid_amount = null;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}