import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_DOCTOR = API_URL + '/api/v1/doctors';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class DoctorsService {
    constructor(private http: HttpClient) { }

    // add patient
    addDoctor(doctorPayload) {
        return this.http.post(API_DOCTOR, doctorPayload, { headers: httpHeaders });
    }

    // get doctor by code
    getDoctorByCode(doctorCode) {
        return this.http.get(API_DOCTOR + '/doctor_code/' + doctorCode, { headers: httpHeaders })
    }
    getAllDoctors() {
        return this.http.get(API_DOCTOR, { headers: httpHeaders })
    }
    getDoctor(doctorId) {
        return this.http.get(API_DOCTOR + '/' + doctorId, { headers: httpHeaders })
    }
    updateDoctor(doctorId, doctor) {
        return this.http.put(API_DOCTOR + '/' + doctorId, doctor, { headers: httpHeaders })
    }
}