import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_ASSOCIATION = API_URL + '/api/v1/associations';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class AssociationsService {
    constructor(private http: HttpClient) { }

    // add patient
    addAssociation(associationPayload) {
        return this.http.post(API_ASSOCIATION, associationPayload, { headers: httpHeaders });
    }

    // get association by code
    getAssociationByCode(associationCode) {
        return this.http.get(API_ASSOCIATION + '/association_code/' + associationCode, { headers: httpHeaders })
    }
    getAllAssociations() {
        return this.http.get(API_ASSOCIATION, { headers: httpHeaders })
    }
}