export class Association {
    id: number;
    association_code: string;
    name: string;
    address: string;
    wilaya_id: number;
    town_id: number;
    phone_number: string;
    fax: string;
    mobile_phone_number_1: string;
    mobile_phone_number_2: string;
    email: string;
    responsible_name: string;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;


    clear() {
        this.id = null;
        this.association_code = '';
        this.name = '';
        this.address = '';
        this.wilaya_id = null;
        this.town_id = null;
        this.phone_number = '';
        this.fax = '';
        this.mobile_phone_number_1 = '';
        this.mobile_phone_number_2 = '';
        this.email = '';
        this.responsible_name = '';
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}