export class InventoryReceipt {
    id:number;
    receipt_code:string;
    po_id:number;
    provider_id:number;
    creation_date:string;
    ship_date:string;
    dutiable:boolean;
    tax_id:number;
    priority:boolean;
    paid:boolean;
    total_amount:number;
    open_amount:number;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear(){
        this.id=null;
        this.receipt_code='';
        this.po_id=null;
        this.provider_id=null;
        this.creation_date='';
        this.ship_date='';
        this.dutiable=false;
        this.tax_id=null;
        this.priority=false;
        this.paid=false;
        this.total_amount=null;
        this.open_amount=null;    
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;  
    }
}