export class Payment {
    id: number;
    payment_code: string;
    patient_id: number;
    payment_date: string;
    payment_amount: number;
    payment_status_id: number;
    payment_type_id: number;
    is_association: boolean;
    discount: number;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.payment_code = '';
        this.patient_id = null;
        this.payment_date = '';
        this.payment_amount = null;
        this.payment_status_id = null;
        this.payment_type_id = null;
        this.is_association = false;
        this.discount = 0;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}