export class Provider {
    id: number;
    provider_code: string;
    description: string;
    provider_type_id: number;
    provider_status_id: number;
    address: string;
    contact_name: string;
    mobile_phone_number: string;
    fix: string;
    fax: string;
    email: string;
    website: string;
    rc: string;
    nif: string;
    ai: string;
    purchase_currency_id: number;
    dutiable: boolean;
    tax_id: number;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.provider_code = '';
        this.description = '';
        this.provider_type_id = null;
        this.provider_status_id = null;
        this.address = '';
        this.contact_name = '';
        this.mobile_phone_number = '';
        this.fix = '';
        this.fax = '';
        this.email = '';
        this.website = '';
        this.rc = '';
        this.nif = '';
        this.ai = '';
        this.purchase_currency_id = null;
        this.dutiable = false;
        this.tax_id = null;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}