import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import {API_URL} from '../../../../env';


const API_PAYMENTS = API_URL + '/api/v1/payments';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');

@Injectable()
export class PaymentsService {
    constructor(private http: HttpClient) { }

    addPayment(payload) {
        return this.http.post(API_PAYMENTS, payload, { headers: httpHeaders });
    }

    getAllDoctors() {
        return this.http.get(API_PAYMENTS, { headers: httpHeaders })
    }

    getPatientsCredit() {
        return this.http.get(API_PAYMENTS + '/patients_credit', { headers: httpHeaders })
    }
    getPatientsCreditAssociation(payload) {
        return this.http.post(API_PAYMENTS + '/patients_credit_2', payload, { headers: httpHeaders })
    }
    getPatientsDiscounts() {
        return this.http.get(API_PAYMENTS + '/patients_discount', { headers: httpHeaders })
    }
    getPaymentsHistory(payload) {
        return this.http.post(API_PAYMENTS + '/history', payload, { headers: httpHeaders });
    }
}