import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { API_URL } from '../../../../env';


const API_PROVIDERS = API_URL + '/api/v1/providers';
const API_PO = API_URL + '/api/v1/purchase_orders';
const API_IR = API_URL + '/api/v1/inventory_receipts';
const API_TRANSACTIONS = API_URL + '/api/v1/transactions';
const API_INVENTORY = API_URL + '/api/v1/inventoryI';
const API_INVENTORY_DETAIL = API_URL + '/api/v1/inventory_detail';
const API_PROVIDERS_PAYMENTS = API_URL + '/api/v1/providers_payments';


const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class InventoryService {
    constructor(private http: HttpClient) { }
    /* providers API */
    addProvider(payload) {
        return this.http.post(API_PROVIDERS, payload, { headers: httpHeaders });
    }
    getProviderByCode(code) {
        return this.http.post(API_PROVIDERS + '/provider_code', { value: code }, { headers: httpHeaders })
    }
    getAllProviders() {
        return this.http.get(API_PROVIDERS, { headers: httpHeaders });
    }
    addProviderPayment(payload) {
        return this.http.post(API_PROVIDERS_PAYMENTS, payload, { headers: httpHeaders });
    }
    /* PO API */
    addPo(payload) {
        return this.http.post(API_PO, payload, { headers: httpHeaders });
    }
    getPoByCode(code) {
        return this.http.post(API_PO + 'attr/po_code', { value: code }, { headers: httpHeaders })
    }
    getAllPo() {
        return this.http.get(API_PO, { headers: httpHeaders });
    }
    getPo(id) {
        return this.http.get(API_PO + '/' + id, { headers: httpHeaders });
    }
    /* IR  API */
    addInventoryReceipt(payload) {
        return this.http.post(API_IR, payload, { headers: httpHeaders });
    }
    getReceptionHistory(payload) {
        return this.http.post(API_IR + '/between_two_dates', payload, { headers: httpHeaders });
    }
    getTransactionsHistory(payload) {
        return this.http.post(API_TRANSACTIONS, payload, { headers: httpHeaders });
    }

    getInventory() {
        return this.http.get(API_INVENTORY, { headers: httpHeaders });
    }
    getProvidersCredit() {
        return this.http.get(API_PROVIDERS + '/providers_credit', { headers: httpHeaders });
    }
    getInventoryDetails(productId) {
        return this.http.get(API_INVENTORY_DETAIL + '/' + productId, { headers: httpHeaders });
    }
    doInventory(productId, payload) {
        return this.http.post(API_INVENTORY + '/' + productId, payload, { headers: httpHeaders });
    }
    VerifySerialNumber(payload) {
        return this.http.post(API_INVENTORY + '/verify_serial_number', payload, { headers: httpHeaders });
    }
    doInventoryDetail(productId, payload) {
        return this.http.post(API_INVENTORY + '/' + productId + '/details', payload, { headers: httpHeaders });
    }
}
