export class Product {
    id: number;
    product_code: string;
    description: string;
    measure_unit_id: number;
    product_type_id: number;
    product_family_id: number;
    unit_price: number;
    purchase_price: number;
    last_purchase_price: number;
    dutiable: boolean;
    tax_id: number;
    serial_number: boolean;
    daily_saled: boolean;
    product_bonus: number;
    is_actif: boolean;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = null;
        this.product_code = '';
        this.description = '';
        this.measure_unit_id = null;
        this.product_type_id = null;
        this.product_family_id = null;
        this.unit_price = null;
        this.purchase_price = null;
        this.last_purchase_price = null;
        this.dutiable = null;
        this.tax_id = null;
        this.serial_number = null;
        this.daily_saled = null;
        this.product_bonus = null;
        this.is_actif = true;
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}