import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { Router } from '@angular/router';
import {API_URL} from '../../../../env';

const API_REPORTS = API_URL + '/api/v1/reports';

const httpHeaders = new HttpHeaders();
httpHeaders.set('Content-Type', 'application/json');
httpHeaders.set('accept', 'application/json');
httpHeaders.set('Access-Control-Allow-Origin', '*');


@Injectable()
export class ReportsService {
    constructor(private http: HttpClient) { }

    getDashboardReports() {
        var date = new Date();
        return this.http.get(API_REPORTS + '/dashboard/?year=2019+&month=12&day='+date.getDate(), { headers: httpHeaders });
    }

    getDashboardReportsYearly() {
        var date = new Date();
        return this.http.get(API_REPORTS + '/dashboard_yearly/?year=2019', { headers: httpHeaders });
    }

}
