export class Patient {
    id: number;
    patient_code: string;
    full_name: string;
    birth_date: string;
    gender_id: number;
    family_status_id: number;
    profession_id: number;
    address:string;
    wilaya_id: number;
    town_id: number;
    social_security_number: string;
    phone_number: string;
    fax: string;
    mobile_phone_number_1: string;
    mobile_phone_number_2: string;
    email: string;
    comment: string;
    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;

    clear() {
        this.id = undefined;
        this.patient_code = '';
        this.full_name = '';
        this.birth_date = '';
        this.gender_id = undefined;
        this.family_status_id = undefined;
        this.profession_id = undefined;
        this.address = '';
        this.wilaya_id = undefined;
        this.town_id = undefined;
        this.social_security_number = '';
        this.phone_number = '';
        this.fax = '';
        this.mobile_phone_number_1 = '';
        this.mobile_phone_number_2 = '';
        this.email = '';
        this.comment = '';
        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}