export class Appointment {
    id: number;
    appointment_code: string;
    patient_id: number;
    appointment_start: string;
    appointment_end: string;
    is_all_day: boolean;
    is_closed: boolean;
    note: string;

    created_ip_address: string;
    last_updated_ip_address: string;
    created_user_id: number;
    last_modified_user_id: number;


    clear() {
        this.id = null;
        this.appointment_code = '';
        this.patient_id = null;
        this.appointment_start = '';
        this.appointment_end = '';
        this.is_all_day = false;
        this.is_closed = false;
        this.note = '';

        this.created_ip_address = '';
        this.last_updated_ip_address = '';
        this.created_user_id = null;
        this.last_modified_user_id = null;
    }
}